/**
 * Преобразование пикселей в rem на основе переменной с базовым размером шрифта.
 * @param  {number} $px Число пикселей (с единицами измерения или без)
 * @param  {string} Число пикселей, размер контентного шрифта на проекте
 * @return {string} Результат преобразования $px в rem
 */
body {
  font-family: "Ubuntu Regular", sans-serif;
  font-size: 1.125rem;
  transition: all 0.3s linear;
}
h1 {
  font-size: 40px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 1280px;
  margin: auto;
}
.main .main-title {
  cursor: pointer;
}
.main .main-content {
  display: none;
  font-weight: normal;
}
